* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Poppins";
}

nav {
  height: 70px;
  width: 100%;
  display: flex;
  padding: 20px 36px;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.1);
}

.grey {
  color: #918e9b;
}

.bold {
  font-weight: bold;
}

.nav--logo {
  max-width: 100px;
}

section {
  padding: 20px;
}

.hero {
  display: flex;
  flex-direction: column;
}

.hero-img-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.hero--photo {
  align-self: center;
  max-width: 800px;
}

.hero--header {
  margin-bottom: 16px;
}

.hero--text {
  margin: 0;
  font-weight: 300;
  font-size: 16px;
  line-height: 110%;
  max-width: 320px;
}

.cards-list {
  display: flex;
  flex-wrap: nowrap;
  gap: 20px;
  overflow-x: auto;
}

.card {
  width: 275px;
  position: relative;
  font-size: 12px;
  flex: 0 0 auto; /* ADD */
  display: flex; /* ADD */
  flex-direction: column; /* ADD */
}

.card--title {
  overflow: hidden;
  text-overflow: ellipsis;
}

.card-image-container {
  height: 100%;
  width: 100%;
  margin-bottom: 10px;
}

.card--image {
  object-fit: cover;
  height: 100%;
  width: 100%;
  border-radius: 10px;
  border: 2px solid black;
}

.card--sts {
  display: flex;
  align-items: center;
}

.card--star {
  width: 14px;
}
.card--price {
  margin-top: auto;
}

.card--badge {
  position: absolute;
  top: 6px;
  left: 6px;
  background-color: white;
  padding: 5px 7px;
  border-radius: 2px;
  font-weight: bold;
}
